import React, {  } from 'react'
import { NavLink as RouterLink } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import MetaMask from '../../MetaMask'

import { logOut } from '../../../store/user/userSlice'

import Logo from '../../../images/logo/logo.png'

import Navbar from '../../Navbar/Navbar'

const Desktop = ({ openMetamaskModel,  closeMetaMaskModal, showMetaMaskModal}) => {


  const dispatch = useDispatch()

  const userSlice = useSelector((state) => state.user)


  const handleLogOut = () => {
    dispatch(logOut())
  }

  return (
    <div>
      <Navbar>
        <div>
          <RouterLink className="logo-container" exact to="/">
            <img className="logo" src={Logo} />
          </RouterLink>
        </div>

      

        <div className="nav-row">
       </div>

          <div className="divider-vertical"></div>

  

        {userSlice.user ? (
             <div className="sub-navbar">
             <div className="nav-item">
               <div className="item">
                 <RouterLink exact activeClassName="active" to="/">
                   Dashboard
                 </RouterLink>
               </div>
             </div>
             <div className="nav-item">
               <div className="item">
                 <RouterLink exact activeClassName="active" to="/users">
                Users
                 </RouterLink>
               </div>
             </div>
             <div className="nav-item">
               <div className="item">
                 <RouterLink exact activeClassName="active" to="/artworks">
                 Artworks
                 </RouterLink>
               </div>
             </div>
             <div className="nav-item">
               <div className="item">
                 <RouterLink exact activeClassName="active" to="/transactions">
                 Transactions
                 </RouterLink>
               </div>
             </div>

             <div className="nav-item">
               <div className="item">
                 <a onClick={handleLogOut}>
                 Logout
                 </a>
               </div>
             </div>
           
            
           </div>
        ) : (
          <div>
            <button onClick={openMetamaskModel} className="connect-button">
              Connect Wallet
            </button>
          </div>
        )}


      </Navbar>

      <MetaMask
        showModal={showMetaMaskModal}
        handleClose={closeMetaMaskModal}
      />
    </div>
  )
}

export default Desktop
