import React from 'react'
import LoadingSvg from '../../images/loading/loading2.svg'
const Loading = () => {
    return (
        <div style={{
            position: 'absolute',
            zIndex: '2000',
            height: '100vh',
            width: '100%',
            top: '0',
            background: '#000',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
          <img src={LoadingSvg} alt="loading"></img>
        </div>

    )
}

export default Loading