import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'


import DesktopView from './view/desktop'
import MobileView from './view/mobile'

import './index.css'

export default function Header(props) {
 
  const mobileView = useSelector((state) => state.layout.MobileView)

  const [showMetaMaskModal, setShowMetaMaskModal] = useState(false)


  const openMetamaskModel = () => {
    setShowMetaMaskModal(true)
  }

  const closeMetaMaskModal = () => {
    setShowMetaMaskModal(false)
  }

  return (
    <div style={{ boxShadow: 'none' }} position="relative" color='transparent'  >
      {mobileView ?
       <MobileView 
       showMetaMaskModal={showMetaMaskModal}
        openMetamaskModel={openMetamaskModel} 
        closeMetaMaskModal={closeMetaMaskModal} /> 
      : <DesktopView 
      showMetaMaskModal={showMetaMaskModal} 
      openMetamaskModel={openMetamaskModel}
       closeMetaMaskModal={closeMetaMaskModal} />}
    </div>
  );
}