import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { findAllStats } from '../../api/users'
import Loading from '../../components/Loading'

const Home = () => {

  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)
  const [loading, setLoading] = useState(true)
  const [stats, setStats] = useState({
    users: 0,
    tranasctions: 0,
    artowrks: 0,
  })
  useEffect(() => {
    console.log('use effect')
    ;(async () => {
      try {
        if (accessToken) {
          console.log('accessToken: ' + accessToken)
          let fetchedStats = await findAllStats(accessToken)
          setStats(fetchedStats)
          setLoading(false)

        }
      } catch (err) {
        console.log(err)
      }
    })()
  }, [accessToken])


  if(!user){
    return(
        <div className="middle-text">
            <div>Login </div>
        </div>
      )
  }

  if(user && user.role !== "admin" ){
      return(
        <div className="middle-text">
            <div>Be a admin</div>
        </div>
      )
  }

  if(loading){
    return <Loading/>
  }

  return (
    <div  className="container">

<div>
    <h1 style={{textAlign:'center', margin: '5% 0'}}>Dashboard</h1>
    </div>

      <div
       
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-evenly',
          alignContent: 'center',
        }}
      >



        <div class="card card-bg" style={{ width: '18rem' }}>
          <div class="card-body">
            <h5 class="card-title">Total Users</h5>
            <h6 class="card-subtitle">{stats.users}</h6>
          </div>
        </div>
        <div class="card card-bg" style={{ width: '18rem' }}>
          <div class="card-body">
            <h5 class="card-title">Total Artworks</h5>
            <h6 class="card-subtitle">{stats.artworks}</h6>
          </div>
        </div>
        <div class="card card-bg" style={{ width: '18rem' }}>
          <div class="card-body">
            <h5 class="card-title">Total Transactions</h5>
            <h6 class="card-subtitle">{stats.transactions}</h6>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home
