import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import Modal from '@material-ui/core/Modal'
import moment from 'moment'
import { findAllArtworks } from '../../api/artwork'

import { isVideo } from '../../utils'
import { IMG_BASE } from '../../constant'
import Clip from '../../components/Clip'
import Loading from '../../components/Loading'

const Artworks = (props) => {
  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)

  const [artworks, setArtworks] = useState([])
  const [showModal, setShowModal] = useState(false)
  const [modalArtwork, setModalArtwork] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    console.log('use effect')
    ;(async () => {
      try {
        if (accessToken) {
          console.log('accessToken: ' + accessToken)
          let fetchedArtworks = await findAllArtworks(accessToken)
          setArtworks(fetchedArtworks)
          setLoading(false)
        }
      } catch (err) {
        console.log(err)
      }
    })()
  }, [accessToken])

  const handleArtworkClick = (e) => {
    e.preventDefault()
    const artworkId = e.target.dataset.id
    artworks.forEach((artwork) => {
      if (artwork._id === artworkId) {
        setModalArtwork(artwork)
      }
    })
    setShowModal(true)
  }

  const ArtworkCol = ({ artwork }) => {
    return (
      <tr>
        {/* <th scope="col">{user._id}</th> */}
        <td scope="col">{moment(artwork.date).format('LLL')}</td>
        <td scope="col">{artwork.tokenId}</td>
        <td scope="col">{artwork.creator}</td>
        <td scope="col">{artwork.owner}</td>
        <td scope="col">{artwork.price / 1e18}</td>
        <td scope="col">{artwork.status}</td>
        <td scope="col">
          <button
            data-id={artwork._id}
            onClick={handleArtworkClick}
            className="btn btn-primary"
          >
            View
          </button>
        </td>
      </tr>
    )
  }

  if (!user) {
    return (
      <div className="middle-text">
        <div>Login </div>
      </div>
    )
  }

  if (user && user.role !== 'admin') {
    return (
      <div className="middle-text">
        <div>Be a admin</div>
      </div>
    )
  }

  if (loading) {
    return <Loading />
  }

  return (
    <div>
      <div className="container">
        <table className="table table-striped table-hover">
          <thead>
            <tr>
              {/* <th scope="col">#</th> */}
              <th scope="col">date</th>
              <th scope="col">token id</th>
              <th scope="col">creator</th>
              <th scope="col">owner</th>
              <th scope="col">price</th>
              <th scope="col">status</th>
              <th scope="col">action</th>
            </tr>
          </thead>
          <tbody>
            {artworks.map((artwork) => {
              return <ArtworkCol key={artwork._id} artwork={artwork} />
            })}
          </tbody>
        </table>

        {modalArtwork ? (
          <Modal
            open={showModal}
            onClose={() => setShowModal(false)}
            className="modal"
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}
          >
            <div className="modal-body">
              <div className="container">
                <div>
                  {isVideo(modalArtwork.artwork) ? (
                    <Clip
                      url={IMG_BASE + modalArtwork.artwork}
                      className="video"
                    />
                  ) : (
                    <img
                      className="img-fuild"
                      src={IMG_BASE + modalArtwork.artwork}
                      className="img"
                    />
                  )}
                </div>
                <h4>{modalArtwork.title}</h4>
                <h6>{modalArtwork.description}</h6>
                <p>tokenId: {modalArtwork.tokenId}</p>
                <p>creator: {modalArtwork.creator}</p>
                <p>owner: {modalArtwork.owner}</p>
                <p>metadata: {modalArtwork.metadata}</p>
                <p>status: {modalArtwork.status}</p>
                <p>
                  endtime:{' '}
                  {modalArtwork.endTime
                    ? modalArtwork.endTime
                    : 'auction ended'}
                </p>
                <p>
                  price: {modalArtwork.price / 1e18} {modalArtwork.currency}{' '}
                </p>
              </div>
            </div>
          </Modal>
        ) : (
          <></>
        )}
      </div>
    </div>
  )
}
export default Artworks
