import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { findAllTransactions } from '../../api/artwork'
import moment from 'moment'
import Loading from '../../components/Loading'

const Transactions = (props) => {
  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)
  const [transactions, setTransactions] = useState([])

  const [loading, setLoading] = useState(true)

  useEffect(() => {
    console.log("use effect")
    ;(async () => {
      try {
        if (accessToken) {
          console.log("accessToken: "+accessToken)
          let fetchedTransactions = await  findAllTransactions(accessToken)
          setTransactions(fetchedTransactions)
          setLoading(false)

        }
      } catch (err) {
        console.log(err)
      }
    })()
  }, [accessToken])


  const TransactionCol = ({transaction}) => {
    return( <tr>
      {/* <th scope="col">{user._id}</th> */}
      <td scope="col">{moment(transaction.date).format('LLL')}</td>
      <td scope="col">{transaction.username}</td>
      <td scope="col">{transaction.tokenId}</td>
      <td scope="col">{transaction.type}</td>
      <td scope="col">{transaction.value / 1e18} {transaction.currency? transaction.currency: 'ETH' }</td>

      <td className="scroll-col" scope="col">{transaction.from}</td>
      <td className="scroll-col" scope="col">{transaction.to}</td>
      <td className="scroll-col" scope="col">{transaction.hash}</td>
 
      </tr>
    )
  }

  if(!user){
    return(
        <div className="middle-text">
            <div>Login </div>
        </div>
      )
  }

  if(user && user.role !== "admin" ){
      return(
        <div className="middle-text">
            <div>Be a admin</div>
        </div>
      )
  }

  if(loading){
    return <Loading/>
  }

  return(

    <div>
<div className="container">
<table className="table table-striped table-hover">
      <thead>
    <tr>
      {/* <th scope="col">#</th> */}
      <th scope="col">date</th>
      <th scope="col">username</th>
      <th scope="col">tokenId</th>
      <th scope="col">type</th>
      <th scope="col">value</th>
      <th scope="col">from</th>
      <th scope="col">to</th>
      <th scope="col">hash</th>

    </tr>
  </thead>
  <tbody>
   {transactions.map((transaction)=>{
     return (<TransactionCol key={transaction._id}  transaction={transaction} />)
   })}
  </tbody>
</table>

</div>

    </div>

  )
}
export default Transactions
