import React, {useState} from 'react'
import {Link } from 'react-router-dom'

//import { ReactComponent as ArrowDownIcon } from '../../images/svg/arrow-down.svg'

import './index.css'

const Footer = () => {

    const [subscription, setSubcription] = useState("")


  return (
    <footer className="footer">
      <div className="footer-container">
        <div className="footer-top">
          <div className="update-form-container">
            <div className="update-form-container2">
              <span className="title">Get the latest Digiplaza updates</span>
              <form className="">
                <div className="form-input">
                  <input placeholder="Your e-mail" type="email" value={subscription} onChange={(e)=> setSubcription(e.target.value)} />
                </div>
                <div className="form-button">
                  <button type="submit">I’m in</button>
                </div>
              </form>
            </div>
          </div>

          <div className="link-list">
            <span className="title">Digiplaza</span>
            <ul className="">
              <li className="">
                <Link to="/">
                  <span className="">Explore</span>
                </Link>
              </li>
              <li>
                <a href="/faq" target="_blank">
                  <span>How it works</span>
                </a>
              </li>
              <li className="">
                <Link to="/creator/create">
                  <span className="">Create</span>
                </Link>
              </li>
              <li className="">
                <a target="_blank">
                  <span className="">Support</span>
                </a>
              </li>
            </ul>
          </div>

          <div className="link-list">
            <span className="title">Community</span>
            <ul className="">
              <li className="">
                <a href="/">
                  <span className="">DIGI Token</span>
                </a>
              </li>
              <li>
                <a target="_blank">
                  <span>Discussion</span>
                </a>
              </li>
              <li className="">
                <a href="/create">
                  <span className="">Voting</span>
                </a>
              </li>
              <li className="">
                <a target="_blank">
                  <span className="">Suggest feature</span>
                </a>
              </li>
            </ul>
          </div>

          <div className="">
            {/* <span className="">Language</span>
            <div className="">
              <div className="" aria-expanded="false">
                <button className="" type="button">
                  <span className="">English</span>
                  <ArrowDownIcon />
                </button>
              </div>
            </div> */}
          </div>
        </div>
      
      <div className="footer-bottom">
          <div className="copyright">
              <span>© Digiplaza, Inc. All rights reserved.</span>
          </div>
          {/* <div>
          <div className="link">
              <a>Terms</a>
          </div>
          <div className="link">
          <a>Privacy</a>
          </div>
          </div> */}
      </div>
      
      </div>
    </footer>
  )
}
export default Footer
