import React, { useState } from 'react'
//import Web3 from 'web3'
//import { web3, connectMetaMask } from '../../services/web3'
import { connectMetaMask, getCurrentAccount } from '../../services/Web3/web3'
// import {currentAccount} from '../../services/Web3/metamask'
import { useSelector, useDispatch } from 'react-redux'
import { Button } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import Modal from '@material-ui/core/Modal'
import { loggedIn } from '../../store/user/userSlice'

import LoadingSvg from '../../images/loading/loading2.svg'
import MetaMaskSvg from '../../images/metamask-logo.svg'

import {APP_NAME, CHAIN_ID} from '../../constant'

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    // border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    borderRadius: '30px',
    '&:focus': {
      outline: 'none',
    },
  },
  metamaskBtn: {
    height: '60px',
    backgroundImage:
      'linear-gradient(267.54deg , rgb(255, 220, 36) 1.63%, rgb(255, 92, 0) 98.05%)',
    borderRadius: '10px',
    width: '80%',
    margin: '20px 0',
    transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
    '&:hover': {
      boxShadow: '0px 8px 15px rgb(0 0 0 / 25%)',
      transform: 'translateY(-2px)',
    },
  },
  metamaskBtnContent: {
    color: '#fff',
    margin: '0px',
    display: 'flex',
    alignIitems: 'center',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: ' 100%',
    padding: '16px 24px',
    fontWeight: '600',
    letterSpacing: '1px',
  },
  retryBtn: {
    height: '60px',
    background: '#000',
    color: '#fff',
    borderRadius: '10px',
    width: '80%',
    margin: '20px 0',
    transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
    '&:hover': {
      background: '#000',
      color: '#fff',
      boxShadow: '0px 8px 15px rgb(0 0 0 / 25%)',
      transform: 'translateY(-2px)',
    },
  },
  linkStyle: {
    display: 'block',
    paddingTop: '6px',
    textDecoration: 'none',
    color: '#000',
  },
}))

const MetaMask = ({ showModal, handleClose }) => {
  const classes = useStyles()
  const dispatch = useDispatch()

  const [isLoading, setLoading] = useState(false)
  const [error, setError] = useState(null)

  // const mobileView = useSelector((state) => state.user)

  //WEB3 CONNECT WALLET
  const handleConnectWallet = async () => {
    try {
      setLoading(true)
      await connectMetaMask()
      if(window.ethereum.chainId !== CHAIN_ID){
        throw {title: "Wrong Network", message: 'Please connect to the smartchain' }
      }


      const publicAddress = await getCurrentAccount()
      let response = await fetch(`/api/users/address/${publicAddress}`)
      let currentUser = await response.json()
      
      let signedMsg = await handleSignMessage(currentUser)
      let { accessToken } = await handleAuthenticate(signedMsg)

      dispatch(loggedIn(accessToken))
      setLoading(false)
      handleClose()
    } catch (err) {
      console.log(err)
      setLoading(false)
      setError(err)
    }
  }

  const handleSignMessage = async ({ publicAddress, nonce }) => {
    return new Promise(async (reslove, reject) => {
      try {
        const msg = `Please sign this message to connect to ${APP_NAME}(${nonce})`
        const params = [msg , publicAddress]
        const method = 'personal_sign'

        const signature = await window.web3.currentProvider.request({ method,params, publicAddress})

        reslove({ publicAddress, signature })
      } catch (err) {
        console.log(err)
        reject({
          title: 'Signature rejected',
          message: 'You need to sign the message to be able to log in.',
        })
      }
    })
  }

  const handleAuthenticate = ({ publicAddress, signature }) => {
    return new Promise((reslove, reject) => {
      fetch(`/api/auth`, {
        body: JSON.stringify({ publicAddress, signature }),
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
      })
        .then((response) => {
          reslove(response.json())
        })
        .catch((err) => {
          reject(err)
        })
    })
  }

  const MetaMaskConnect = () => {
    return (
      <div>
        <h2>Connect your wallet</h2>
        <p style={{ width: '350px' }}>
          By connecting your wallet, you agree to our Terms of Service and our
          Privacy Policy.
        </p>

        <Button className={classes.metamaskBtn} onClick={handleConnectWallet}>
          <div className={classes.metamaskBtnContent}>
            {isLoading ? 'Connecting...' : 'MetaMask'}
            <img
              src={isLoading ? LoadingSvg : MetaMaskSvg}
              style={{ height: '40px' }}
            />
          </div>
        </Button>

        <div>
          <a href="#" className={classes.linkStyle}>
            New to Smart Chain?
          </a>
          <a href="#" className={classes.linkStyle}>
            Learn more about wallets
          </a>
        </div>
      </div>
    )
  }
  const MetaMaskError = ({ title, message }) => {
    return (
      <div>
        <h2>{title ? title : 'Error'}</h2>
        <p style={{ width: '350px' }}>{message ? message : 'Internal Error'}</p>

        <Button className={classes.retryBtn} onClick={() => setError(null)}>
          Retry
        </Button>
      </div>
    )
  }

  return (
    <Modal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      className={classes.modal}
      open={showModal}
      onClose={handleClose}
    >
      <div style={{ textAlign: 'center' }} className={classes.paper}>
        {error ? MetaMaskError(error) : MetaMaskConnect()}
      </div>
    </Modal>
  )
}

export default MetaMask
