import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { findAllUsers } from '../../api/users'
import Loading from '../../components/Loading'


const Users = (props) => {
  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)
  const [users, setUsers] = useState([])

  const [loading, setLoading] = useState(true)

  useEffect(() => {
    console.log("use effect")
    ;(async () => {
      try {
        if (accessToken) {
          console.log("accessToken: "+accessToken)
          let fetchedUsers = await findAllUsers(accessToken)
          setUsers(fetchedUsers)
          setLoading(false)

        }
      } catch (err) {
        console.log(err)
      }
    })()
  }, [accessToken])


  const UserCol = ({user}) => {
    return( <tr>
      {/* <th scope="col">{user._id}</th> */}
      <td scope="col">{user.fullname}</td>
      <td scope="col">{user.username}</td>
      <td scope="col">{user.email}</td>
      <td scope="col">{user.publicAddress}</td>
      <td scope="col">{user.role}</td>
      <td scope="col">
        <button className="btn btn-danger">Ban</button>
      </td>
      </tr>
    )
  }

  if(!user){
    return(
        <div className="middle-text">
            <div>Login </div>
        </div>
      )
  }

  if(user && user.role !== "admin" ){
      return(
        <div className="middle-text">
            <div>Be a admin</div>
        </div>
      )
  }

  if(loading){
    return <Loading/>
  }
  return(

    <div>
<div className="container">
<table className="table table-striped table-hover">
      <thead>
    <tr>
      {/* <th scope="col">#</th> */}
      <th scope="col">full name</th>
      <th scope="col">username</th>
      <th scope="col">email</th>
      <th scope="col">publicAddress</th>
      <th scope="col">role</th>
      <th scope="col">action</th>
    </tr>
  </thead>
  <tbody>
   {users.map((user)=>{
     return (<UserCol key={user._id}  user={user} />)
   })}
  </tbody>
</table>

</div>

    </div>

  )
}
export default Users
